// Library imports.
import axios from "axios";

// App imports.
import { config } from "./config";


// The worker that will perform SMTP operations.
export class Worker {


  /**
   * Sand a message via SMTP.
   *
   * @param ktbTo      The Email to send the message to.
   * @param ktbFrom    The Email address it's from (from config).
   * @param ktbSubject The subject of the message.
   * @param ktbMessage The message itself.
   */
  public async sendMessage(ktbTo: string, ktbFrom: string, ktbSubject: string, ktbMessage: string): Promise<void> {

    console.log("SMTP.Worker.sendMessage()");

    await axios.post(`${config.serverAddress}/messages`, { to : ktbTo, from : ktbFrom, subject : ktbSubject,
      text : ktbMessage
    });

  }

} 
